@import "../../scss/color.scss";

.post {
    
    border-bottom: 1px solid rgba(202, 202, 202, 0.6);
    margin-bottom: 30px;
    
    h2 {
        color: $blue;
        font-size: 40px;
        font-weight: 900;
        margin: 12px 0;

        &:hover {
            color: $blue-sea;
        }
    }

    a {
        text-decoration: none;
    }

    .tag {
        display: inline-block;
        color: #fff;
        padding: 4px;
        background-color: #2a2a2d;
        border-radius: 4px;
        font-size: 14px;
        margin-bottom: 8px;

        &:hover {
            background-color: #53b4ff;
        }
    }

    .tag + .tag {
        margin-left: 10px;
    }
}
