@import "../../scss/color.scss";


.link {
    background-color: $black;
    color: $while;
    text-decoration: none;
    font-weight: 700;
    font-size: 1.2rem;
    text-align: center;
    padding: 18px;

    &:hover {
        background-color: $black-light;
        color: $blue;
    }
}

#avata {
    position: absolute;
    top:16px;
    left: 50%;
    transform: translateX(-50%);
    height: 90px;
    width: 90px;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: 1px 1px 15px 1px #000;


    img {
        width: 100%;
        height: 100%;
    }
}
